import React, {useState} from 'react'
import { styled, Box } from '@mui/system';
import ModalUnstyled from '@mui/base/ModalUnstyled';
import { Close } from '@styled-icons/evaicons-solid';
import './FeedbackModal.css';
import {base} from '../components/Make/SectionTypes/baseTypes'
import lodash from 'lodash'

const StyledModal = styled(ModalUnstyled)`
  position: fixed;
  z-index: 1300;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;
const Backdrop = styled('div')`
  z-index: -1;
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-tap-highlight-color: transparent;
`;
const style = {
    width: 800,
    height: 620,
    bgcolor: 'white',
    boxShadow: '18px 18px 36px rgba(0, 0, 0, 0.15), inset 6px 4px 16px rgba(108, 99, 255, 0.1), inset -12px -8px 16px rgba(108, 99, 255, 0.25)',
    borderRadius:2,
    p: 2,
    px: 4,
    pb: 3,
  };

function FeedbackModal({open, setOpen, setFeedback, deploy}) {
    const [quest, goQuest] = useState(false);
    const [page, setPage] = useState(1);
    const [complete, setComplete] = useState(false);
    const [clicked, setClicked] = useState(false);

    const [path, setPath] = useState('');
    const [difficul, setDifficul] = useState('');
    const [inconv, setInconv] = useState('');
    const [satisfy, setSatisfy] = useState('');
    const [time, setTime] = useState('');
    const [func, setFunc] = useState('');
    const [comment, setComment] = useState('');
    const [recom, setRecom] = useState('');

    const toPrev = () => {
        if(page>1)
            setPage(page-1)
        else
            return
    }

    const toNext = () => {
        if(page<8 && clicked)
           {
               setPage(page+1);
               setClicked(false);
           }
        else return
    }

    const toComplete = () => {
        if(clicked)
        {
            setComplete(true)
            goQuest(false)
            setClicked(false)
            
        }
        else return
    }

    const pathList = [
        {label: ' ', value: 'Kakao'},
        {label: '', value: 'Disquiet'},
        {label: ' ', value: 'Recommend'},
        {label: '', value: 'etc'},
    ]

    const diffList = [
        {label: '1', value: '1'},{label: '2', value: '2'},{label: '3', value: '3'},{label: '4', value: '4'},{label: '5', value: '5'},
    ]

    const recommendList = [
        {label: '1', value: '1'}, {label: '2', value: '2'}, {label: '3', value: '3'}, {label: '4', value: '4'}, {label: '5', value: '5'}, {label: '6', value: '6'}, {label: '7', value: '7'},  {label: '8', value: '8'}, {label: '9', value: '9'}, {label: '10', value: '10'},
    ]

    const inconvList = [
        {label: ' ', value: ' '},
        {label: '  ', value:'  '},
        {label: ' ', value:' '},
        {label: '  ', value:'  '},
        {label: '', value:''},    
    ]

    const satisfyList = [
        {label:' ', value:' '},
        {label:'  ', value:'  '},
        {label:' ', value:' '},
        {label:' ', value:' '},
        {label:'', value:''},
    ]

    const timeList = [
        {label:'5 ', value:' '},
        {label:'5  30 ', value:'5  30 '},
        {label:'30  1  ', value:'30  1  '},
        {label:'1  3 ', value:'1  3 '},
        {label:'3 ', value:'3 '},
    ]

    const returnQuestions = () => {
        switch(page){
            case 1: return(
                <>
                <div className="big-title question">
                       Surfee  ?
                </div>
                <div className="column-ans">
                    {pathList.map(item => (
                        <div className="round-ans-button" onClick={() => {setPath(item.value); setClicked(true);}}>
                        {item.label}
                    </div>
                    ))}
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 2: return(
                <>
                <div className="big-title question">
                Surfee     ?
                </div>
                <div className="row-ans">
                    <div className="circle-text"></div>
                    {diffList.map(item => (
                        <div className="circle-ans-button" onClick={() => {setDifficul(item.value); setClicked(true);}}>{item.label}</div>
                    ))}
                    <div className="circle-text"></div>
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 3: return(
                <>
                <div className="big-title question">
                Surfee     ?
                </div>
                <div className="column-ans">
                    {inconvList.map(item=>(
                        <div className="round-ans-button" onClick={() => {setInconv(item.value); setClicked(true);}}>
                        {item.label}
                        </div>
                    ))}
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 4: return(
                <>
                <div className="big-title question">
                Surfee     ?
                </div>
                <div className="column-ans">
                    {satisfyList.map(item => (
                        <div className="round-ans-button" onClick={() => {setSatisfy(item.value); setClicked(true);}}>
                        {item.label}
                        </div>
                    ))}
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 5: return(
                <>
                <div className="big-title question">
                    ?
                </div>
                <div className="column-ans">
                    {timeList.map(item => (
                        <div className="round-ans-button" onClick={() => {setTime(item.value); setClicked(true);}}>
                        {item.label}
                        </div>
                    ))}
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 6: return(
                <>
                <div className="big-title question">
                     !
                </div>
                <div className="modal-text-input">
                <input className="modal-text-input inputbox" onChange={(e)=>{setFunc(e.target.value); setClicked(true);}}/>
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 7: return(
                <>
                <div className="big-title question">
                Surfee ,     !
                </div>
                <div className="modal-text-input">
                <input className="modal-text-input inputbox" onChange={(e)=>{setComment(e.target.value); setClicked(true);}}/>
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toNext}></div>
                </div>
                </>
            )
            case 8: return(
                <>
                <div className="big-title question">
                , Surfee  , ,     ?
                </div>
                <div className="row-ans" style={{width: '700px'}}>
                    <div className="circle-text"></div>
                    {recommendList.map(item => (
                        <div className="circle-ans-button" onClick={()=>{setRecom(item.value); setClicked(true);}}>{item.label}</div>
                    ))}
                    <div className="circle-text"></div>
                </div>
                <div className="prev-next-fbuttons">
                    <div className="prev-next-fbutton" onClick={toPrev}></div>
                    <div className="prev-next-fbutton" onClick={toComplete}></div>
                </div>
                </>
            )
        }
    }

    return (
        <div>
            <StyledModal
                aria-labelledby="unstyled-modal-title"
                aria-describedby="unstyled-modal-description"
                open={open}
                onClose={() => setOpen(!open)}
                BackdropComponent={Backdrop}
            >
                <Box sx={style}>
                    <div className="modal-top__title">
                        <div className="modal-close-button" onClick={() => setOpen(false)}>
                            <Close size="30" />
                        </div>
                        <div className="big-title">Surfee   </div>
                    </div>
                    { quest ? (
                        <>
                            <div className="questnum">Q{page}</div>
                            {returnQuestions()}
                        </> 
                    ) :
                    (<>
                        {
                            complete ? (
                                <>
                                <div className="big-title" style={{color:'black', marginTop: '70px'}}>!</div>
                                <div className="big-title sub">
                                        <br />
                                      Surfee  :)
                                </div>
                                <div className="feed-button" style={{width: '167px'}} onClick={()=>{deploy(true)}}>
                                    
                                </div>
                                </>
                            ):
                            (
                                <>      
                                <div className="big-title" style={{color:'black', marginTop: '70px'}}> ?</div>
                                <div className="big-title sub">
                                     <span style={{color:`var(--main-color)`}}> </span>  <br />
                                    Surfee      ,<br />
                                           :)
                                </div>
                                <div className="big-title small-text">
                                      <span style={{color:`var(--main-color)`}}>8</span>,   <span style={{color:`var(--main-color)`}}>1</span>.
                                </div>
                                <div className="feed-button" onClick={()=>{goQuest(true)}}>
                                       
                                </div>
                                </>
                            )
                        }
                    </>)
                    }
                    
                </Box>
            </StyledModal>
        </div>
    )
}

export default FeedbackModal
